import React, { Component, Fragment } from "react";
import IntlMessages from "../../../helpers/IntlMessages";
import { Colxx, Separator } from "../../../components/common/CustomBootstrap";
import Breadcrumb from "../../../containers/navs/Breadcrumb";
import 'react-tabs/style/react-tabs.css';
import {
  Row,
  Card,
  CardBody,
  CardTitle,
  Input,
  Button,
  Table,
  Label,
  Form,
  Modal , 
  ModalHeader,
  ModalBody,
  ModalFooter
} from "reactstrap";
import "react-tagsinput/react-tagsinput.css";
import "rc-switch/assets/index.css";
import "rc-slider/assets/index.css";
import "react-rater/lib/react-rater.css";
import moment, { relativeTimeRounding } from "moment";
import TimezonePicker from 'react-timezone-picker';
import Select from "react-select";
import HubService from '../../../service/graphql/Hub'
import TimezoneSelect from 'react-timezone-select'
import ReactTimezonePicker from 'react-timezone';
import timezonelist from "../../../data/timezonelist";
import times from "../../../data/times";


import CustomSelectInput from "../../../components/common/CustomSelectInput";
import Pagination from "../../../components/pages/Pagination";

export default class HubTimes extends Component {
  constructor(props) {
    super(props);

    this.state = {
      showDeleteModal:false,
      hubTimings:[],
      selectedHubTimingId:null,
      selectedHubTiming:[],
      selectedOption:'',
      orderPlaceCutTime:null,
      orderConfirmCutTime:null,
      pickupStartTime:null,
      pickupEndTime:null,
      deliveryStartTime:null,
      deliveryEndTime:null,
      timezone:'',
      totalPage:2,
      currentPage:1,
      timezoneList:timezonelist,
      times:times
    };
  }

  componentDidMount(){
    this.getHubTimings()
    console.log('props...',this.props)
  }
  
  getHubTimings(){
    if(this.props.match.params.id &&  this.props.match.params.id!=="0"){
      const params ={
        _id:this.props.match.params.id,
      }
      HubService.getHubTimings(params,(response)=>{
        console.log("getHubTimings res...", response)
        if (response.status === 1) {
          const res = response.data.hub_timings?response.data.hub_timings:[];
          const timezoneName=response.data.timzone?response.data.timzone:''
          this.setState({timezoneName})
          console.log("timezone res...", timezoneName)
          const timezone=timezoneName?this.state.timezoneList.find(item=>item.value===timezoneName):''
          this.setState({timezone})
          if(res&&res.length>0){
            res.map((item)=>{
              item.order_place_cut_time=item.order_place_cut_time?(times.find(o=>o.value===item.order_place_cut_time)).label:''
              item.order_confirm_cut_time=item.order_confirm_cut_time?(times.find(o=>o.value===item.order_confirm_cut_time)).label:''
              item.pickup_start_time=item.pickup_start_time?(times.find(o=>o.value===item.pickup_start_time)).label:''
              item.pickup_end_time=item.pickup_end_time?(times.find(o=>o.value===item.pickup_end_time)).label:''
              item.delivery_start_time=item.delivery_start_time?(times.find(o=>o.value===item.delivery_start_time)).label:''
              item.delivery_end_time=item.delivery_end_time?(times.find(o=>o.value===item.delivery_end_time)).label:''
            })
          } 
          if(res){
            this.setState({hubTimings:res})
            if(res&&(Math.round(res.length)/5)<2){
              this.setState({totalPage:2})
            }
            else{
              this.setState({totalPage:(Math.round(res.length))/5})
            }
          } 
          else{
          }
        } else {
        }
      })
    }
    else{
    }   
  } 
  showDeleteModal(id,e){
    console.log('selected id...',id)
    this.setState({selectedHubTimingId:id})
    this.setState({showDeleteModal:true})
  }
  showAddNewModal(id,e){
    let {hubTimings,selectedHubTiming}=this.state
    let orderPlaceCutTime=null
    let orderConfirmCutTime=null
    let pickupStartTime=null
    let pickupEndTime=null
    let deliveryStartTime=null
    let deliveryEndTime=null
    this.setState({selectedHubTimingId:id})
    if(id){
      selectedHubTiming=hubTimings.find(item=>item.hub_timing_id===id)
      orderPlaceCutTime=times.find(item=>item.label===selectedHubTiming.order_place_cut_time)
      orderConfirmCutTime=times.find(item=>item.label===selectedHubTiming.order_confirm_cut_time)
      pickupStartTime=times.find(item=>item.label===selectedHubTiming.pickup_start_time)
      pickupEndTime=times.find(item=>item.label===selectedHubTiming.pickup_end_time)
      deliveryStartTime=times.find(item=>item.label===selectedHubTiming.delivery_start_time)
      deliveryEndTime=times.find(item=>item.label===selectedHubTiming.delivery_end_time)
    }
    else{
      selectedHubTiming=[]
    }
    console.log('selectedHubTiming...',selectedHubTiming)
    this.setState({orderPlaceCutTime,orderConfirmCutTime,pickupStartTime,pickupEndTime,deliveryStartTime,deliveryEndTime})
    this.setState({selectedHubTiming})
    this.setState({showAddNewModal:true})
    console.log('index..',this.state.selectedHubTimingId)
  }
  toggleAddNewModal =()=>{
    this.setState({
      showAddNewModal: !this.state.showAddNewModal
    });
  }
  toggle =()=>{
    this.setState({
      showDeleteModal: !this.state.showDeleteModal
    });
  }
  
  postHubTimings(){
    let {selectedHubTiming}=this.state
    console.log("selectedHubTiming...", selectedHubTiming)
    if(this.props.match.params.id &&  this.props.match.params.id!=="0"){
      const params ={
        _id:this.props.match.params.id,
        hubTimingId:this.state.selectedHubTimingId,
        orderPlaceCutTime:selectedHubTiming.order_place_cut_time?times.find(item=>item.label===selectedHubTiming.order_place_cut_time).value:'',
        orderConfirmCutTime:selectedHubTiming.order_confirm_cut_time?times.find(item=>item.label===selectedHubTiming.order_confirm_cut_time).value:'',
        pickupStartTime:selectedHubTiming.pickup_start_time?times.find(item=>item.label===selectedHubTiming.pickup_start_time).value:'',
        pickupEndTime:selectedHubTiming.pickup_end_time?times.find(item=>item.label===selectedHubTiming.pickup_end_time).value:'',
        deliveryStartTime:selectedHubTiming.delivery_start_time?times.find(item=>item.label===selectedHubTiming.delivery_start_time).value:'',
        deliveryEndTime:selectedHubTiming.delivery_end_time?times.find(item=>item.label===selectedHubTiming.delivery_end_time).value:''
      }
      console.log('params...',params)
      HubService.postHubTimings(params,(response)=>{
        console.log("postHubTimings res...", response)
        if (response.status === 1) {
          this.getHubTimings()
          this.toggleAddNewModal()
        } else {
        }
      })
    }
    else{
    }    
  } 
  setHubTimeZone(){
    let {selectedHubTiming}=this.state
    if(this.props.match.params.id &&  this.props.match.params.id!=="0"){
      const params ={
        _id:this.props.match.params.id,
        hubTimezone:this.state.timezone.value,
      }
      console.log('params...',params)
      HubService.setHubTimeZone(params,(response)=>{
        console.log("setHubTimeZone res...", response)
        if (response.status === 1) {

        } else {
        }
      })
    }
    else{
    }    
  } 
  deleteHubTiming() {
    if(this.state.selectedHubTimingId && this.state.selectedHubTimingId!==null){
      const params ={
        hub_timing_id:this.state.selectedHubTimingId,
      }
      HubService.deleteHubTiming(params,(response)=>{
        console.log("deleteHubTiming res...", response)
        if (response.status === 1) {
          this.getHubTimings()
        } else {
        }
      })
    }
    else{
    } 
    this.toggle()
  }
  onclose(e){
    this.props.history.push(`/app/hubs/hub-list`)
  }
  handleChange(data,type) {
    console.log('value,type...',data,type)
    let {selectedHubTiming} = this.state
    selectedHubTiming[type] = data.label?data.label:''
    this.setState({selectedHubTiming})
    if(type==='order_place_cut_time'){
      this.setState({orderPlaceCutTime:data})
    }
    else if(type==='order_confirm_cut_time'){
      this.setState({orderConfirmCutTime:data})
    }
    else if(type==='pickup_start_time'){
      this.setState({pickupStartTime:data})
    }
    else if(type==='pickup_end_time'){
      this.setState({pickupEndTime:data})
    }
    else if(type==='delivery_start_time'){
      this.setState({deliveryStartTime:data})
    }
    else if(type==='delivery_end_time'){
      this.setState({deliveryEndTime:data})
    }
    
  };
  toggle =()=>{
    this.setState({
      showDeleteModal: !this.state.showDeleteModal
    });
  }
  toggleAddNewModal =()=>{
    this.setState({
      showAddNewModal: !this.state.showAddNewModal
    });
  }
  setSelectedTimezone =timezone=>{
    console.log('timezone...',timezone)
    this.setState({
      timezone
    });
  }
  onChangePage(i){
    console.log('pagenation...',i)
    this.setState({currentPage:i})
  }
    render() {
      const {hubTimings,timezone} = this.state
        return (
            <Fragment>
              <Row>
                <Colxx xxs="12">
                  <Breadcrumb heading="Hub Timings Detail" match={this.props.match} />
                  <Separator className="mb-5" />
                </Colxx>
              </Row>
              <Row className="mb-4">
                <Colxx xxs="12" md="12">
                  <Card>
                    <CardBody>
                    
                        <Row>
                          <Colxx xxs="12"  style={{flex:1}}>
                            <Label className="mt-4">
                              Timezone
                            </Label>
                            <div className='select-wrapper'>
                            <Row>
                              <Colxx xxs="12" md="3" style={{flex:1}}>
                                {/* <TimezoneSelect
                                  value={timezone}
                                  onChange={this.setSelectedTimezone}
                                  // allName
                                  abbrev
                                  placeholder   = "Select timezone..."
                                /> */}
                                <Select
                                  components={{ Input: CustomSelectInput }}
                                  className="react-select"
                                  classNamePrefix="react-select"
                                  name="form-field-name"
                                  value={this.state.timezone}
                                  onChange={(e)=>{this.setSelectedTimezone(e)}}
                                  options={this.state.timezoneList}
                                />
                                {/* <TimezonePicker
                                  onChange={this.onChangeTimezone}
                                  defaultValue={'America/New_York'}
                                  unselectLabel="No Timezone"
                                  style={{
                                      borderRadius: '0.5rem',
                                      background: 'teal',
                                      color: 'white',
                                  }}
                                /> */}
                                {/* <ReactTimezonePicker
                                  value="Asia/Yerevan"
                                  onChange={timezone => console.log('New Timezone Selected:', timezone)}
                                  inputProps={{
                                    placeholder: 'Select Timezone...',
                                    name: 'timezone',
                                  }}
                                /> */}
                              </Colxx>
                              <Colxx xxs="12" md="6" style={{flex:1}}>
                                <Button color="primary" size="sm"
                                  onClick={this.setHubTimeZone.bind(this)}
                                  >
                                  Submit
                                </Button>{" "}
                              </Colxx>
                            </Row>
                            </div>
                          </Colxx>
                        </Row>
                        <Row>
                        <Colxx xxs="12" md="6" style={{flex:1}}>
                          <Label className="mt-4">
                            Hub Timings
                          </Label>
                          
                        </Colxx>
                        <Colxx xxs="12" md="6"className="display-flex flex-row-reverse top-right-button-container align-item-right">
                          <Button
                            color="primary"
                            size="sm"
                            className="top-right-button"
                            onClick={this.showAddNewModal.bind(this,null)}>
                            <IntlMessages id="pages.add-new" />
                          </Button>
                        </Colxx>
                      </Row>
                    <Card>
                      <CardBody>
                        <Table>
                          <thead>
                            <tr>
                              <th>#</th>
                              <th>Order Time</th>
                              <th>Pickup Time</th>
                              <th>Delivery Time</th>
                              <th>Action</th>
                            </tr>
                          </thead>
                          <tbody>
                            {hubTimings.map((item, index) => {
                              return (
                                <tr
                                  key={"specialDays_" + index}
                                >
                                  <td>
                                    {item._id}
                                  </td>
                                  <td>
                                    {item.order_place_cut_time} - {item.order_confirm_cut_time}
                                  </td>
                                  <td>
                                    {item.pickup_start_time} - {item.pickup_end_time}
                                  </td>
                                  <td>
                                    {item.delivery_start_time} - {item.delivery_end_time}
                                  </td>
                                  <td>
                                    <div className="action-group">
                                      <i style = {{cursor:'pointer'}} className="iconsminds-refresh" 
                                        onClick={this.showAddNewModal.bind(this,item._id)}
                                        // onClick={this.refreshShopSpecialDay.bind(this,index)}
                                        />
                                      <i
                                        style = {{marginLeft:'5px'}} className="simple-icon-trash" 
                                        onClick={this.showDeleteModal.bind(this, item._id)}
                                      />
                                    </div>
                                  </td>
                                </tr>
                              );
                            })}
                          </tbody>
                        </Table>
                        <Pagination
                          currentPage={this.state.currentPage}
                          totalPage={this.state.totalPage}
                          onChangePage={i => this.onChangePage(i)}
                        />
                      </CardBody>
                    </Card>
                  
                    <Button color="secondary" className="mt-4" outline
                      onClick={()=>{
                        this.onclose()}}>
                        <IntlMessages id="forms.close" />
                      </Button>
                    </CardBody>
                </Card>
              </Colxx>
            </Row>
            <Modal isOpen={this.state.showAddNewModal} toggle={this.toggleAddNewModal}wrapClassName="modal-right"
              backdrop="static">
              <ModalHeader toggle={this.toggleAddNewModal}>
              {this.state.selectedHubTimingId==null?"Add New Hub Timings": "Edit Hub Timings"}
                {/* <IntlMessages id="modal.modal-title" /> */}
              </ModalHeader>
              <ModalBody >
                <Label>
                  Order Time
                </Label>
                <Row>
                  <Colxx xxs="12" md="6" className="mb-5">
                    <label>
                      <IntlMessages id="forms.from" />
                    </label>
                    <Select
                      components={{ Input: CustomSelectInput }}
                      className="react-select"
                      classNamePrefix="react-select"
                      name="form-field-name"
                      value={this.state.orderPlaceCutTime}
                      onChange={(e)=>{this.handleChange(e,'order_place_cut_time')}}
                      options={this.state.times}
                    />
                  </Colxx>
                  <Colxx xxs="12" md="6">
                    <label>
                      <IntlMessages id="forms.to" />
                    </label>
                    <Select
                      components={{ Input: CustomSelectInput }}
                      className="react-select"
                      classNamePrefix="react-select"
                      name="form-field-name"
                      value={this.state.orderConfirmCutTime}
                      onChange={(e)=>{this.handleChange(e,'order_confirm_cut_time')}}
                      options={this.state.times}
                    />
                  </Colxx>
                </Row>
                <Label>
                  Pickup Time
                </Label>
                <Row>
                  <Colxx xxs="12" md="6" className="mb-5">
                    <label>
                      <IntlMessages id="forms.from" />
                    </label>
                    <Select
                      components={{ Input: CustomSelectInput }}
                      className="react-select"
                      classNamePrefix="react-select"
                      name="form-field-name"
                      value={this.state.pickupStartTime}
                      onChange={(e)=>{this.handleChange(e,'pickup_start_time')}}
                      options={this.state.times}
                    />
                  </Colxx>
                  <Colxx xxs="12" md="6">
                    <label>
                      <IntlMessages id="forms.to" />
                    </label>
                    <Select
                      components={{ Input: CustomSelectInput }}
                      className="react-select"
                      classNamePrefix="react-select"
                      name="form-field-name"
                      value={this.state.pickupEndTime}
                      onChange={(e)=>{this.handleChange(e,'pickup_end_time')}}
                      options={this.state.times}
                    />
                  </Colxx>
                </Row>
                <Label>
                  Delivery Time
                </Label>
                <Row>
                  <Colxx xxs="12" md="6" className="mb-5">
                    <label>
                      <IntlMessages id="forms.from" />
                    </label>
                    <Select
                      components={{ Input: CustomSelectInput }}
                      className="react-select"
                      classNamePrefix="react-select"
                      name="form-field-name"
                      value={this.state.deliveryStartTime}
                      onChange={(e)=>{this.handleChange(e,'delivery_start_time')}}
                      options={this.state.times}
                    />
                  </Colxx>
                  <Colxx xxs="12" md="6">
                    <label>
                      <IntlMessages id="forms.to" />
                    </label>
                    <Select
                      components={{ Input: CustomSelectInput }}
                      className="react-select"
                      classNamePrefix="react-select"
                      name="form-field-name"
                      value={this.state.deliveryEndTime}
                      onChange={(e)=>{this.handleChange(e,'delivery_end_time')}}
                      options={this.state.times}
                    />
                  </Colxx>
                </Row>
              </ModalBody>
              <ModalFooter>
              <Button color="secondary" outline onClick={this.toggleAddNewModal}>
                Cancel
              </Button>
              <Button color="primary" 
                onClick={this.postHubTimings.bind(this)}
                >
                Submit
              </Button>{" "}
              </ModalFooter>
            </Modal>
            <Modal isOpen={this.state.showDeleteModal} toggle={this.toggle}>
              <ModalHeader toggle={this.toggle}>
                Warning
                {/* <IntlMessages id="modal.modal-title" /> */}
              </ModalHeader>
              <ModalBody>
                Are you sure to delete this times?
              </ModalBody>
              <ModalFooter>
                <Button color="primary" 
                  onClick={this.deleteHubTiming.bind(this)}>
                  Yes
                </Button>{" "}
                <Button color="secondary" outline onClick={this.toggle}>
                  Cancel
                </Button>
              </ModalFooter>
            </Modal>
          </Fragment>
        )
    }
}
